div .card-deck {
    flex-direction: row; 
    display: flex;
    flex-wrap: wrap;
    align-content: center;
}

div .card {
    margin: 0 auto;
    text-align: center;
    align-content: center;
    float: none;
}

.sightlife {
    padding-top: 20px;
    margin-left: 10px;
}

.footer {
    padding-top: 100px;
}

section {
    display: block;
    background-color:  rgb(213,210,233);
    padding-top: 0.5rem;
    padding-bottom: 4rem;
    /* text-align: center; */
}

section:nth-child(odd) {
    background-color: rgb(255,255,255);
}

section h2 {
    line-height: 2rem;
    margin-top: 5rem;
    margin-bottom: 2rem;
    text-align: center;
}

section p {
    line-height: 2rem;
    font-size: 1.2rem;

    /* 
        Need to change margins for team information
        separetely
    */

}

.learnMore a {
    font-size: 15px;
}

.section-content {
    margin-right: 1rem;
    margin-left: 1rem;
}

.flex-container {
    display: flex;
    flex-wrap: wrap;
}

.card {
    border-radius: 50%;
    position: relative;
    background-color: transparent;
    border: none;
    display: inline;
    min-width: 200px;
}

.column {
    text-align: center;
    margin-right: auto;
    margin-left: auto;
}

.column h3 {
    margin-top: 2rem;
    font-size: 1.55rem;
}

.column p {
    max-width: 100rem;
    font-size: 1rem;
    line-height: 2rem;
    
}

