} */

#contact img {
    width: 13rem;
    height: 13rem;
    border-radius: 50%;
    border-style: solid;
    border-color: white;
    border-width: 0.5rem;
}

.feature {
    margin-top: 1rem;
}

.feature h3 {
    font-size: 1.5rem;
}
