.button {
    width: 50px;
}



.logo {
    padding-left: 1em;
    padding-right: 0.25em;
    padding-bottom: 2em;
    /*background-image: url('./img/logo.png');*/
    background-size: contain;
    background-repeat: no-repeat;
    margin-right: 4rem;
}

button:hover {
    background-color: rgb(213,210,233); 
}

#title {
    /* padding-top: 10rem;
    padding-bottom: 6rem; */
    margin-top: 5rem;
    margin-bottom: 5rem;
    background-color: rgb(255,255,255);
    text-align: center;
    /* background-image: linear-gradient(to bottom,  rgba(255,168,76,0.6) 0%,rgba(255,123,13,0.6) 100%), url('https://images.unsplash.com/photo-1446824505046-e43605ffb17f'); */
	background-blend-mode: soft-light;
	background-size: cover;
	background-position: center center;
	padding: 2rem;
}

#title h1 {
    font-size: 2rem;
    display: inline-block;
    margin-right: 15rem;
}

#title p {
    /* font-size: 2rem; */
    font-size: 1.25rem;
    font-weight: 400;
    margin-right: 15rem;
    /* margin-left: 20rem; */
}

#title #me {
    display: inline;
    color: rgb(93, 45, 153);
}

.screen {
    height: 30rem;
}

.feature-screen {
    height: 400px;
}

#contact {
    text-align: none;
}

#contact p {
    margin: 0;
}

#our-team, #documentation {
    text-align: center;
}

#departments {
    margin: 0;
    align-content: center;
}

#departments .cards img {
    max-width: 100px;
}

@media (min-width: 300px) {
    #title h1 {
        margin-right: 0;
    }

    #title p {
        margin-right: 0px;
    }

    div .card-deck {
        flex-direction: row; 
        display: flex;
        align-content: center;
    }

    .section-content {
        margin-right: 5rem;
        margin-left: 5rem;
        align-content: center;
    }
}

@media (min-width: 550px) {
    section h2 {
        line-height: 4rem;
    }
    h2 {
        font-size: 2rem;
    }

    p {
        font-size: 1rem;
    }

    .section-content {
        margin-right: 5rem;
        margin-left: 5rem;
        align-content: center;
    }

    nav {
        font-size: 1.0rem;
        margin: 0;
        padding: 0;
        background-color: white;
    }

    #title div {
        margin-left: 5rem;
    }

    #contact {
        margin-left: 5rem;
    }

    /* #title h1 {
        margin-right: 0;
    }
    

    #title p {
        margin-right: 10px;
    } */

    .feature {
        margin-bottom: 3rem;
    }

    .feature h3 {
        font-size: 2rem;
        padding-top: 5rem;
    }

    .feature p, .feature h3 {
        padding-left: 1rem;
        padding-right: 1rem;
    }

    #title h1 {
        margin-right:  60px;
    }

    #title p {
        /* font-size: 2rem; */
        /* font-size: 1.25rem;
        font-weight: 400; */
        margin-right: 60px;
        /* margin-left: 20rem; */
    }

}

@media (min-width: 950px) {
    .flex-container {
        flex-wrap: nowrap;
    }

    /* 
    .button {
        font-size: 1.5rem;
        padding: 16px 24px;
    } */

    #title h1 {
        margin-right: 5rem;
    }
    

    #title p {
        /* font-size: 2rem; */
        /* font-size: 1.25rem;
        font-weight: 400; */
        margin-right: 5rem;
        /* margin-left: 20rem; */
    }

}

@media (min-width: 1200px) {
    .feature p, .feature h3 {
        padding-left: 12.5rem;
        padding-right: 12.5rem;
    }

    .section-content {
        margin-right: 5rem;
        margin-left: 5rem;
    }

    #title div {
        margin-left: 5rem;
    }
}

@media (min-width: 1500px) {
    .feature p, .feature h3 {
        padding-left: 10rem;
        padding-right: 10rem;
    }

    .section-content {
        margin-right: 13rem;
        margin-left: 13rem;
        align-content: center;
    }

    .p intro {
        text-align: center;
    }
}